<template>
    <div id="app">
        <v-header></v-header>
        <v-rightSider></v-rightSider>
        <div id="content" class="content">
            <v-courseHeader :courseData="courseData" :bus="bus"></v-courseHeader>
            <mu-row gutter>
                <mu-col width="100" tablet="70" desktop="70" class="tab-detail">
                    <mu-row>
                        <mu-col width="100" tablet="50" desktop="50">
                            <div class="tab">
                                <mu-tabs :value="activeTab" @change="handleTabChange">
                                    <mu-tab value="introduction" title="介绍" />
                                    <mu-tab value="chapter" title="章节" />
                                    <mu-tab value="discussion" title="讨论区" />
                                </mu-tabs>
                            </div>
                        </mu-col>
                    </mu-row>
                    <div>
                        <!-- 课程介绍 -->
                        <p class="introduction tab-page" :class="{active: activeTab === 'introduction'}">{{courseData.description}}</p>
                        <!-- 课程章节 -->
                        <div class="chapter tab-page" :class="{active: activeTab === 'chapter'}">
                            <mu-list>
                                <mu-list-item v-for="(section, sectionIndex) in chapter.sections" :title="'第'+(sectionIndex+1)+'章：'+section.name" :key="section.id" toggleNested>
                                    <mu-list-item v-for="(chapter,chapterIndex) in section.chapter" :title="'第'+(chapterIndex+1)+'课时：'+chapter.name" :key="chapter.id" slot="nested"></mu-list-item>
                                </mu-list-item>
                            </mu-list>
                        </div>
                        <!-- 讨论区 -->
                        <div class="chapter tab-page" :class="{active: activeTab === 'discussion'}">
                            <mu-sub-header>讨论区</mu-sub-header>
                            <p>欢迎进入课程讨论区，你可以与本课程的老师和同学在这里交流。如果你有课程相关的问题，请发到老师答疑区；经验、思考、创意、作品、转帖请发到综合讨论区。欢迎分享，鼓励原创，杜绝广告，请大家共同维护一个包容、积极、相互支持的交流氛围，谢谢。</p>
                            <mu-raised-button label="发起主题" class="demo-raised-button" secondary/>
                            <mu-sub-header>子版块</mu-sub-header>
                            <mu-list>
                                <mu-list-item class="sub-list-title">全部</mu-list-item>
                                <mu-list-item class="sub-list-title">老师答疑区</mu-list-item>
                                <mu-list-item class="sub-list-title">综合讨论区</mu-list-item>
                            </mu-list>
                            <mu-sub-header>全部主题</mu-sub-header>
                            <mu-list>
                                <mu-list-item title="发布人" :open="close" toggleNested disabled>
                                    <mu-avatar slot="left" src="/images/uicon.jpg" />
                                    <span slot="describe">
                                        <span>6月10日</span>
                                    <span style="float:right">
                                            浏览 （1）   
                                            回复（0）  
                                            点赞（0）
                                        </span>
                                    </span>
                                    <p>主题标题</p>
                                    <mu-list-item slot="nested">全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题全部主题</mu-list-item>
                                </mu-list-item>
                            </mu-list>
                        </div>
                    </div>
                </mu-col>
                <mu-col width="100" tablet="30" desktop="30">
                    <div class="comment-list">
                        <mu-list>
                            <mu-sub-header>评价</mu-sub-header>
                            <mu-list-item :title="comment.name" v-for="comment in courseData.comments" :key="comment.id">
                                <mu-avatar :src="comment.post" slot="leftAvatar" />
                                <mu-icon value="grade" v-for="item in comment.star" :key="item" />
                                <mu-list-item slot="right" class="time">
                                    {{comment.time}}
                                </mu-list-item>
                            </mu-list-item>
                        </mu-list>
                    </div>
                    <div class="learner-list">
                        <mu-list>
                            <mu-sub-header>{{student_num}}人在学</mu-sub-header>
                            <mu-list-item class="learner-avatar" v-for="student in courseData.students" :key="student.id">
                                <mu-avatar slot="left" :src="student.post" />
                            </mu-list-item>
                        </mu-list>
                    </div>
                </mu-col>
            </mu-row>
            <mu-snackbar v-if="snackbar" :message="alertText" @actionClick="hideSnackbar" @close="hideSnackbar" style="text-align:center"/>
        </div>
        <v-footer></v-footer>
    </div>
</template>
<script>
import header from 'components/common/header/header.vue'
import footer from 'components/common/footer/footer.vue'
import rightSider from 'components/common/rightSider/rightSider.vue'

import courseHeader from 'entry/courseDetail/courseHeader.vue'


const ERR_OK = 0
import Vue from 'vue'
export default {
    data() {
            return {
                courseData: {},
                activeTab: 'discussion',
                chapter: {},
                close: false,
                bus: new Vue(),
                snackbar: false,
                alertText:'未知错误'
            }
        },
        components: {
            'v-header': header,
            'v-footer': footer,
            'v-rightSider': rightSider,
            'v-courseHeader': courseHeader
        },
        created() {
            console.log(this.cid)
            this.$http.get('/course/' + this.cid).then((response) => {
                    response = response.body
                        // console.log(response)
                    if (response.failure.length === 0) {
                        this.courseData = response.success
                    } else {
                        alert(response.failure[0])
                    }

                }),
                this.$http.get('/course/' + this.cid + '/section').then((response) => {
                    response = response.body
                        // console.log(response)
                    if (response.failure.length === 0) {
                        this.chapter = response.success
                        console.log(this.chapter)
                    } else {
                        alert(response.failure[0])
                    }

                });
                if (this.bus) {
                    const _self = this;
                    this.bus.$on("alertFlag", function(msg) {
                        console.log("接收到其他组件传递的数据：");
                        console.log(msg)
                        _self.snackbar = msg.flag;
                        _self.alertText = msg.text;
                        _self.showSnackbar();
                    });
                }
        },
        methods: {
            handleUrl() {
                var url = location.search; //获取url中"?"符后的字串 
                var theRequest = new Object();
                if (url.indexOf("?") != -1) {
                    var str = url.substr(1);
                    var strs = str.split("&");
                    for (var i = 0; i < strs.length; i++) {
                        theRequest[strs[i].split("=")[0]] = decodeURI(strs[i].split("=")[1]);
                    }
                }
                return theRequest;
            },
            handleTabChange(val) {
                this.activeTab = val
            },
            handleActive() {
                window.alert('tab active')
            },
            showSnackbar() {
                this.snackbar = true
                if (this.snackTimer) clearTimeout(this.snackTimer)
                this.snackTimer = setTimeout(() => {
                    this.snackbar = false
                }, 1000)
            },
            hideSnackbar() {
                this.snackbar = false
                if (this.snackTimer) clearTimeout(this.snackTimer)
            }
        },
        computed: {
            cid: function() {
                if (typeof this.handleUrl()['id'] == 'undefined') {
                    return 0;
                } else {
                    return this.handleUrl()['id']
                }

            },
            student_num() {
                try {
                    return this.courseData.students.length
                } catch (e) {
                    console.log('数据读取中')

                }
            }
        }
}
</script>
<style lang="less">
@main-color: #009688;
.content {
    padding: 50px 100px;
    margin-top: 50px;
    .tab {
        position: relative;
        z-index: 0
    }
    .tab-detail {
        border: 1px solid #ccc;
        padding: 15px;
        // height: 500px;
    }
}

.comment-list {
    border: 1px solid #ccc;
    .time {
        .mu-item {
            display: inline-block;
            padding-right: 80px;
            font-size: 12px;
            color: #ccc;
        }
    }
    .mu-icon {
        font-size: 16px;
        color: #ffc107
    }
}

.learner-list {
    margin-top: 15px;
    border: 1px solid #ccc;
    .learner-avatar {
        float: left;
    }
}

.tab-page {
    display: none;
}

.tab-page.active {
    display: block;
}

.sub-list-title {
    border-bottom: 1px solid #ccc
}
</style>



